import React, { useState, useRef } from 'react'
import { useAppSelector } from '../../../store/hooks'
import { datasetService } from '../../../api/services'
import styles from './ExcelUpload.module.css'

interface ExcelUploadProps {
  onUploadSuccess?: (dataset: any) => void
  onUploadError?: (error: string) => void
}

/**
 * Excel Upload Component
 * Allows users to upload Excel datasets for evaluation
 */
const ExcelUpload: React.FC<ExcelUploadProps> = ({ onUploadSuccess, onUploadError }) => {
  const fileInputRef = useRef<HTMLInputElement>(null)
  const { selectedProject } = useAppSelector(state => state.project)

  const [isLoading, setIsLoading] = useState(false)
  const [uploadProgress, setUploadProgress] = useState(0)
  const [selectedFile, setSelectedFile] = useState<File | null>(null)
  const [previewData, setPreviewData] = useState<any>(null)
  const [error, setError] = useState<string | null>(null)

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return

    if (!file.name.endsWith('.xlsx') && !file.name.endsWith('.xls')) {
      setError('❌ Please upload an Excel file (.xlsx or .xls)')
      return
    }

    if (file.size > 50 * 1024 * 1024) {
      setError('❌ File size exceeds 50MB limit')
      return
    }

    setError(null)
    setSelectedFile(file)
    setUploadProgress(0)
  }

  const handleUpload = async () => {
    if (!selectedFile) {
      setError('❌ Please select a file first')
      return
    }

    if (!selectedProject) {
      setError('❌ Please select a project first')
      return
    }

    setIsLoading(true)
    setError(null)

    try {
      const dataset = await datasetService.uploadDataset(
        selectedProject.id,
        selectedFile,
        (progressEvent: any) => {
          const progress = Math.round((progressEvent.loaded / progressEvent.total) * 100)
          setUploadProgress(progress)
        }
      )

      const preview = await datasetService.previewDataset(dataset.id, 10)
      setPreviewData(preview)

      setSelectedFile(null)
      setUploadProgress(0)
      if (fileInputRef.current) {
        fileInputRef.current.value = ''
      }

      onUploadSuccess?.(dataset)
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Failed to upload file'
      setError(`❌ ${message}`)
      onUploadError?.(message)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className={styles.container}>
      <div className={styles.uploadBox}>
        <div className={styles.uploadIcon}>📤</div>
        
        <h3>Upload Excel Dataset</h3>
        <p className={styles.subtitle}>Drag and drop or click to select</p>

        <input
          ref={fileInputRef}
          type="file"
          accept=".xlsx,.xls"
          onChange={handleFileSelect}
          className={styles.fileInput}
          disabled={isLoading}
        />

        <button
          className={styles.selectBtn}
          onClick={() => fileInputRef.current?.click()}
          disabled={isLoading}
        >
          Browse Files
        </button>

        {selectedFile && (
          <div className={styles.selectedFile}>
            <p>✅ Selected: <strong>{selectedFile.name}</strong></p>
            <p className={styles.fileSize}>Size: {(selectedFile.size / 1024 / 1024).toFixed(2)} MB</p>
          </div>
        )}

        {error && <div className={styles.error}>{error}</div>}

        {isLoading && (
          <div className={styles.progressContainer}>
            <div className={styles.progressBar}>
              <div className={styles.progress} style={{ width: `${uploadProgress}%` }} />
            </div>
            <p className={styles.progressText}>{uploadProgress}% Uploading...</p>
          </div>
        )}

        {selectedFile && !isLoading && (
          <button className={styles.uploadBtn} onClick={handleUpload} disabled={!selectedFile}>
            Upload Dataset
          </button>
        )}
      </div>

      {previewData && (
        <div className={styles.previewContainer}>
          <h4>📋 Dataset Preview ({previewData.preview_count} rows)</h4>
          
          <div className={styles.previewInfo}>
            <p><strong>Total Rows:</strong> {previewData.total_rows}</p>
            <p><strong>Columns:</strong> {previewData.headers.join(', ')}</p>
          </div>

          <div className={styles.tableContainer}>
            <table className={styles.previewTable}>
              <thead>
                <tr>
                  {previewData.headers.map((header: string, idx: number) => (
                    <th key={idx}>{header}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {previewData.preview_rows.map((row: any, rowIdx: number) => (
                  <tr key={rowIdx}>
                    {previewData.headers.map((header: string, colIdx: number) => (
                      <td key={colIdx}>{String(row[header] || '-').substring(0, 50)}</td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  )
}

export default ExcelUpload
